@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&display=swap);
.main-header {
    height: 30px;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--color-darkslategray);
}

.header-links-div {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.header-link {
    text-decoration: none;
    color: var(--color-white);
    padding: 0 5px;;
}

@media (max-width: 850px) {
    .main-header {
        justify-content: center;
    }
}
.main-footer {
    height: 30px;
    padding: 20px 0px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-darkslategray);
    position: absolute;
    bottom: 0;
}

.social-media-links {
    float: left;
}
.main-about-me {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.main-projects {
    display: flex;
    justify-content: center;
    align-items: center;
}
:root {
  --color-turquoise: #c4d4d8;
  --color-coral: #ed7947;
  --color-dimgray: #4c5d67;
  --color-darkslategray: #37474f;
  --color-black: #0b1f34;
  --color-white: #fff;
}

body, html {
  padding: 0;
  margin: 0;
}

* {
  font-family: 'Play', sans-serif;
}

body {
  background: #fff;
  background: var(--color-white);
}

a {
  text-decoration: none;
}

.text-primary {
  color: #c4d4d8;
  color: var(--color-turquoise);
}

.text-secondary {
  color: #ed7947;
  color: var(--color-coral);
}

button {
  border: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}
